<template>
	<div class="container" @keypress.enter="search">
 <el-card class="box-card">
  <div slot="header" class="clearfix">
    <span></span>
    <el-button style="float: right; padding: 3px 0" type="text" @click="handleOpDetail"></el-button>
  </div>
  <el-alert
    title=""
    type="success"
    description="/ "
    show-icon>
  </el-alert>
  		
		<el-input placeholder="" v-model="query.videoName" class="input-with-select">
    <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
  </el-input>
  <Right :rights="rights" :module="module" :currentRow="currentRow" :tableData="tableData" :moduleId="18" v-on:initRight="initRight" v-on:deleteRow="deleteRow"></Right>
  
		 <el-table
    :data="tableData"
    style="width: 100%" @sort-change="sortFun" v-loading="loading"
    highlight-current-row 
    @current-change="handleRowChange"
    >
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="">
            <span>{{ props.row.id }}</span>
          </el-form-item>
         <el-form-item label="IP">
            <span>{{ props.row.ipAddr }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.port }}</span>
          </el-form-item>
           <el-form-item label="">
            <span>{{ props.row.videoName }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label="ID"
      prop="id" sortable="custom">
    </el-table-column>
    <el-table-column
      label=""
      prop="videoName" sortable="custom">
    </el-table-column>
  </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[2, 5, 10, 20]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
     </el-card>

	</div>
</template>

<script>
	import Right from '@/components/Right'
	export default {
		created(){
			this.search()
		},
    data() {
      return {
      	module:'videoConfig',
      	loading:false,
      	pageSize : 10,
    	pageNum : 1,
    	sort : 'desc',
    	column :'factory_id',
    	total : 0,
    	query:{//
    		videoName:''
    	},
        tableData: [],
        rights:[],
        currentRow : null
      }
      
    },
    methods :{
    	sortFun(s){//
		this.sort=s.order=='descending'?'desc':'asc'
		this.column=s.prop.replace(/([A-Z])/g,"_$1").toLowerCase()//_
		this.search()
    	},
       handleSizeChange(val) {//
        this.pageSize=val;
        this.search()
      },
      handleCurrentChange(val) {//
        this.pageNum=val
        this.search()
      },
      handleRowChange(val){
      	this.currentRow =val
      },
    search(){//
    	this.loading=true
				this.$http({
  				method: 'post',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/'+this.module+'/page/',
  				params: {
    				access_token:this.token,
    				pageSize : this.pageSize,
    				pageNum : this.pageNum,
    				sort : this.sort,
    				column : this.column
  				},data :this.queryData
  				
				}).then(response=>{
					if(response.status==200){
					this.tableData=response.data.records
					this.total=response.data.total
					}
					this.loading=false
				}).catch(error => {
						 this.loading=false
				if (error.response) {
     			 //  2xx 
     		
      					if(error.response.status==403){
      							if(error.response.data.exception){//
      						this.$message({message:'',type:'error',showClose:true});	
      						}else{
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      						}
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}			
          })
    },initRight(payload){//
				this.rights=payload.rights
	},deleteRow(payload){//
				this.tableData.remove(payload.currentRow)
				this.currentRow=null
				
	}
    },
    computed:{
    	token(){
    		return this.$store.state.token
    	},
    	queryData(){
    		if(this.$store.state.userType==''){
    			return {
    				videoName : this.query.videoName
    			}
    		}
    		
    		return {
    				videoName : this.query.videoName,
    				factory_id : this.$store.state.factory 
    		}
    		
    	}
    	
    },
    components:{
    	Right
    }
  }
</script>

<style>
		.container{
		width:100%;
		margin-top: 20px;
	}
	.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>


