<template>
	<div class="container" @keypress.enter="search" >
		<el-input placeholder="" v-model="query.xmmc" class="input-with-select">
	<!--el-button slot="prepend" icon="el-icon-plus" @click="add"></el-button-->
    <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
  </el-input>
		 <el-table
    :data="tableData"
    style="width: 100%" @sort-change="sortFun" v-loading="loading">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="">
            <span>{{ props.row.sbsj }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.bm }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.yg }}</span>
          </el-form-item>
            <el-form-item label="">
            <span>{{ props.row.xmbh }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>******</span>
          </el-form-item>
           <el-form-item label="">
            <span>******</span>
          </el-form-item>
             <el-form-item label="">
            <span>{{ props.row.lxfs }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.xmmc }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.sjly }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.yshtje }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      prop="xmbh" sortable="custom">
    </el-table-column>
    <el-table-column
      label=""
      prop="xmmc" sortable="custom">
    </el-table-column>
    <!--
  <el-table-column
      fixed="right"
      label=""
      width="100">
      <template slot-scope="scope">
        <el-button @click="handleView(scope.row.id)" type="text" size="small"></el-button>
      </template>
    </el-table-column>
    -->
  </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[2, 5, 10, 20]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
	</div>
	
</template>
<script>
	export default {
		created(){
			this.search()
		},
    data() {
      return {
      	module:'shangjiInfo',
      	loading:false,
      	pageSize : 10,
    	pageNum : 1,
    	sort : 'desc',
    	column :'sbsj',
    	total : 0,
    	query:{//
    		xmmc:'',
    		gszt:1
    	},
        tableData: []
      }
      
    },
    methods :{
    	sortFun(s){//
		this.sort=s.order=='descending'?'desc':'asc'
		this.column=s.prop.replace(/([A-Z])/g,"_$1").toLowerCase()//_
		this.search()
    	},
       handleSizeChange(val) {//
        this.pageSize=val;
        this.search()
      },
      handleCurrentChange(val) {//
        this.pageNum=val
        this.search()
      },
      add(){
      	this.$router.push({ path: ('/shangji/start') })
      },
      handleView(id){
      	this.$router.push({ path: (this.module+'/view/'+id) })
      },
      handleEdit(id){
      	this.$router.push({ path: (this.module+'/edit/'+id) })
      },
    search(){//
    	this.loading=true
				this.$http({
  				method: 'post',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/'+this.module+'/page/',
  				params: {
    				access_token:this.token,
    				pageSize : this.pageSize,
    				pageNum : this.pageNum,
    				sort : this.sort,
    				column : this.column
  				},
  				data: this.query
				}).then(response=>{
					if(response.status==200){
					this.tableData=response.data.records
					this.total=response.data.total
					}
					this.loading=false
				}).catch(error => {
						 this.loading=false
				if (error.response) {
     			 //  2xx 
     		
      					if(error.response.status==403){
      						//Forbidden
      						//org.springframework.security.access.AccessDeniedException
      						//
      						
      						if(error.response.data.exception){//
      						this.$message({message:'',type:'error',showClose:true});	
      						}else{
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      						}
      						
      						
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}			
          })
    }
    },
    computed:{
    	token(){
    		return this.$store.state.token
    	}
    }
  }
</script>
<style scoped="scoped">
	.container{
		width:100%;
		margin-top: 20px;
	}
	.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>


