<template>
	<el-row>
  <template v-for="right in rights">
 
  	<el-button v-if="right.rightTxt==''" type="success"  icon="el-icon-plus" @click="add"></el-button>
  	<el-button v-if="right.rightTxt==''" type="primary" icon="el-icon-edit" @click="handleEdit"></el-button>
  	<el-button v-if="right.rightTxt==''" type="danger" icon="el-icon-delete" @click="handleDelete"></el-button>
  	<el-button v-if="right.rightTxt==''" type="info" icon="el-icon-share" @click="handleRight"></el-button>
  	<el-button v-if="right.rightTxt==''" type="info" icon="el-icon-share" @click="handlePause">{{currentRow!=null?(currentRow.status=='1'?'':''):''}}</el-button>
  	
  </template>
  </el-row>
</template>

<script>
export default {
  name: 'Right',
  
  created(){
  	this.loadright()
  },
  
  props:{
  rights: Array,
  module:String,
  currentRow:Object,
  tableData:Array,
  moduleId:Number
  },methods:{
  	  add(){
      	this.$router.push({ path: (this.module+'/add') })
      },
      handleView(id){
      	this.$router.push({ path: (this.module+'/view/'+id) })
      },
      handleEdit(id){
      	if( this.currentRow){
      	this.$router.push({ path: (this.module+'/edit/'+this.currentRow.id) })	
      	}else{
      	this.$message({message:'',type:'error',showClose:true});	
      	}
  },handleRight(id){
  		if( this.currentRow){
  			this.$router.push({ path: (this.module+'/right/'+this.currentRow.id)})
      	}else{
      	this.$message({message:'',type:'error',showClose:true});	
      	}
      	
      },handleDelete(){//
					if( this.currentRow){
						  this.$confirm(', ?', '', {
          					confirmButtonText: '',
         					 cancelButtonText: '',
          					type: 'warning'
        }).then(() => {
         this.$http({
  				method: 'delete',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/'+this.module+'/'+this.currentRow.id+'/',
  				params: {
    				access_token:this.token,
  				},data :{}
  				
				}).then(response=>{
					if(response.status==200){
					this.$message({message:'',type:'success',showClose:true});
					 this.$emit('deleteRow',{currentRow:this.currentRow})
					}
				}).catch(error => {
				if (error.response) {
     			 //  2xx 
     		
      					if(error.response.status==403){
      							if(error.response.data.exception){//
      						this.$message({message:'',type:'error',showClose:true});	
      						}else{
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      						}
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}			
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      	}else{
      	this.$message({message:'',type:'error',showClose:true});	
      	}
    },loadright(){//
				this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/right/'+this.moduleId+'/user/'+this.$store.state.username+'/',
  				params: {
  					access_token:this.token
  				},data :{
  				}
				}).then(response=>{
					if(response.status==200){
					//this.rights=response.data
					 this.$emit('initRight',{rights:response.data})
					}
				}).catch(error => {
				if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      							if(error.response.data.exception){//
      						this.$message({message:'',type:'error',showClose:true});	
      						}else{
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      						}
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}			
         	 })
				},
				handlePause(){
					if( this.currentRow){
					this.$http({
  				method: 'put',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/'+this.module+'/pause',
  				params: {
    				access_token:this.$store.state.token
  				},
  				data: {
  					id:this.currentRow.id,
  					status:this.currentRow.status
  				}
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
					this.$emit('changeStatus',{currentRow:this.currentRow})
					/*
						for(let index in this.tableData){//
      						if(this.tableData[index].id=id){
      						this.tableData[index].status=(status=='1'?'2':'1')
      						}
      					}*/
				}).catch(error => {
					if (error.response) {
     			 //  2xx 
     			
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
      }else{
      		this.$message({message:'',type:'error',showClose:true});
      }
      }
  },computed:{
    	token(){
    		return this.$store.state.token
    	}
    }
  }
</script>




