<template>
	<div class="container">
	<el-form ref="form" :model="form" label-width="80px" v-loading="loading">
  <el-form-item label="">
    <el-input v-model="form.txt" clearable></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="form.url" clearable></el-input>
  </el-form-item>
  <el-form-item label="status">
  	<el-select v-model="form.status" placeholder="">
       <el-option label="" :value='0'></el-option>
       <el-option label="" :value='1'></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    	<el-select v-model="form.pid" placeholder="">
    		<el-option :label="''" :value='0'></el-option>
        <el-option v-for="p in parents" :label="p.txt" :value='p.id' :key="p.id"></el-option>
    </el-select>
    </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onCreate"></el-button>
    <el-button></el-button>
  </el-form-item>
</el-form>	
</div>
</template>
<script>
	
	export default{
		created(){
			this.loadparents()
		}, 
		data(){
			return {
				loading:false,
				module:'menu',
				parents:[],
				form:{
					
				}
			}
		},
		methods:{
			handleADFocus(){
				 this.$notify({
          title: '',
          message: '0/1 10 WEBIOS 111',
          type: 'warning'
        });
			},
		loadparents(){
			this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: 'http://192.168.0.166:8090/'+this.module+'?status=1',
  				params: {
    				access_token:this.$store.state.token
  				}
				}).then(response=>{
					this.parents=response.data
				}).catch(error => {
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
  					 // console.log(error.config);
					//this.$message({message:'/',type:'error',showClose:true});
          })
		},
    	onCreate(){//
    	this.loading=true
				this.$http({
  				method: 'post',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/'+this.module,
  				params: {
    				access_token:this.$store.state.token
  				},
  				data: this.form
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
					this.loading=false
					this.loadparents()					
					
				}).catch(error => {
					this.loading=false
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      							if(error.response.data.exception){//
      						this.$message({message:'',type:'error',showClose:true});	
      						}else{
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      						}
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
    }
			
		}
		
		
	}
</script>
	
<style scoped="scoped">
.container{
		width:100%;
		margin-top: 20px;
	}	
</style>


