<template>
	<div class="container">
		<el-card class="box-card" >
  <div slot="header" class="clearfix">
    <span></span>
    <el-button style="float: right; padding: 3px 0" type="text" @click="handleOpDetail"></el-button>
  </div>
<el-form ref="form" :model="form"  :rules="rules" label-width="80px" v-loading="loading">
  <el-form-item label="" prop="phone">
    <el-input v-model="form.phone" >
    </el-input>
  </el-form-item>
   <el-form-item label="" prop="code">
    <el-input v-model="form.code"  >
    	<el-button  slot="append" type="primary" @click="handleGetCode" :disabled="disabled">{{handleLabelCode}}</el-button>
    </el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onCreate"></el-button>
  </el-form-item>
</el-form>	
</el-card>
		
	
</div>
</template>
<script>
	
	export default{
		created(){//
			
		}, 
		data(){
			
			
			return {
				loading:false,
				roles:[],
				readonly:true,
				disabled :false ,//
				time : 120, //
				form:{
				phone:'',
				code:''
			},rules:{
			 phone:[
			 { required: true, message: '', trigger: 'blur' },
			 { min: 11, max: 11, message: '11', trigger: 'blur' }
			 ],
			 code:[
			 {required: true, message: '', trigger: 'blur'}
			 ]
			}
			}
		},
		methods:{
    	onCreate(){//
    		
    		
    	 this.$refs["form"].validate((valid) => {
          if (valid) {
          	this.loading=true
				this.$http({
  				method: 'put',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/user/phone',
  				params: {
    				access_token:this.$store.state.token,
    				username:this.$store.state.username,
    				phone:this.form.phone,
    				code:this.form.code
  				},
  				data: {}
				}).then(response=>{
					
					this.loading=false
					this.$message({message:'',type:'success',showClose:true});
					this.disabled=false;
				}).catch(error => {
					this.loading=false
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
          	
          } else {
            return false;
          }
        });
    	
    	
    },handleGetCode(){
    	this.disabled=true
    	this.time=120
    	this.downTime()
    	this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/user/phoneCode',
  				params: {
  					access_token:this.$store.state.token,
    				phone:this.form.phone
  				},
  				data: {}
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
				
				}).catch(error => {
					this.disabled=false
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
    	
    },downTime(timer){
			if(this.disabled){
			if(timer){
				clearTimeout(timer)
			}
			if(this.time>1){
				this.time--;
			let newTimer=setTimeout(newTimer=>{
				this.downTime(newTimer)
			},1000);
			}else{
				this.disabled=false
			}
		}
		}
			
		},computed:{
		handleLabelCode(){
				if(this.disabled){
					return this.time+'s'+' '
				}
				else{
					return ''
				}
			}
		}
		
		
	}
</script>
	
<style scoped="scoped">
.container{
		width:100%;
		margin-top: 20px;
	}	
</style>


