<template>
	<div class="container">
	<el-form ref="form" :model="form" label-width="200px" v-loading="loading">
  <el-form-item label="">
    <el-input v-model="form.bh" clearable></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="form.name" clearable></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="form.addr" clearable></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model="form.phone" clearable></el-input>
  </el-form-item>
  <el-form-item label="">
  	 <el-select v-model="form.owner" placeholder="">
      <el-option v-for="owner in owners" :label="owner.realname" :value="owner.id" :key="owner.id"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
     <el-date-picker
      v-model="form.startDay"
      align="right"
      type="date"
      placeholder=""
      style="width: 100%;"
      value-format="yyyy-MM-dd"
     >
    </el-date-picker>
  </el-form-item>
  <el-form-item label="">
    <el-date-picker
      v-model="form.endDay"
      align="right"
      type="date"
      placeholder=""
      style="width: 100%;"
      value-format="yyyy-MM-dd"
     ></el-date-picker>
  </el-form-item>
  <el-form-item label="">
  	<el-input-number v-model="form.max"  :min="1" :max="100" label=""></el-input-number>
  </el-form-item>
   <el-form-item label="">
    <el-select v-model="form.status" placeholder="">
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
      <el-option label="" value=""></el-option>
    </el-select>
  </el-form-item>
  
  <el-form-item>
    <el-button type="primary" @click="onCreate"></el-button>
    <el-button></el-button>
  </el-form-item>
</el-form>	
</div>
</template>
<script>
	
	export default{
		created(){
			this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: 'http://192.168.0.166:8090/user',
  				params: {
    				access_token:this.$store.state.token
  				}
				}).then(response=>{
					this.owners=response.data
				}).catch(error => {
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
  					 // console.log(error.config);
					//this.$message({message:'/',type:'error',showClose:true});
          })
			
		}, 
		data(){
			return {
				loading:false,
				module:'factory',
				owners:[],
				form:{
					max:1,
					status:''
				}
			}
		},
		methods:{
    	onCreate(){//
    	this.loading=true
				this.$http({
  				method: 'post',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/'+this.module,
  				params: {
    				access_token:this.$store.state.token
  				},
  				data: this.form
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
					this.loading=false
				}).catch(error => {
					this.loading=false
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
    }
			
		}
		
		
	}
</script>
	
<style scoped="scoped">
.container{
		width:100%;
		margin-top: 20px;
	}	
</style>


