<template>
	<div class="container">
<el-steps :active="active" finish-status="success">
  <el-step title=""></el-step>
  <el-step title=""></el-step>
  <el-step title=""></el-step>
</el-steps>



<el-card class="box-card" v-if="active==0">
  <div slot="header" class="clearfix">
    <span></span>
    <el-button style="float: right; padding: 3px 0" type="text" @click="handleOpDetail"></el-button>
  </div>
  <el-alert
    title=""
    type="success"
    description="10   olvi1oq3c4  cjmv1c0bc2 "
    show-icon>
  </el-alert>
<el-form ref="form1" :model="form1"   label-width="200px" v-loading="loading">
  <el-form-item label="" prop="registerCode">
    <el-input v-model="form1.registerCode" clearable></el-input>
  </el-form-item>

  
</el-form>	
</el-card>



<el-card class="box-card" v-if="active==1">
  <div slot="header" class="clearfix">
    
  </div>
<el-form ref="form2" :model="form2"  :rules="rules2" label-width="200px" v-loading="loading">
<el-form-item label="" prop="phone">
    <el-input v-model="form2.phone" clearable></el-input>
     <el-button @click="handleGetCode" :disabled="disabled">{{handleLabelCode}}</el-button>
  </el-form-item>
  

<el-form-item label="" prop="code">
    <el-input v-model="form2.code" clearable></el-input>
</el-form-item>
 
</el-form>	
</el-card>

<el-card class="box-card" v-if="active==2">
  <div slot="header" class="clearfix">
    
  </div>
<el-form ref="form3" :model="form3"  :rules="rules3" label-width="200px" v-loading="loading">
<el-form-item label="" prop="phone">
    <el-input v-model="form3.phone" readonly></el-input>
</el-form-item>
<el-form-item label="" prop="username">
    <el-input v-model="form3.username" clearable></el-input>
</el-form-item>
<el-form-item label="" prop="realname">
    <el-input v-model="form3.realname" clearable></el-input>
</el-form-item>  
<el-form-item label="" prop="password">
	 <el-input :type="passtype" placeholder="" v-model="form3.password" class="input-with-select">
    <el-button slot="append"  @click="passtype=='text'?passtype='password':passtype='text'">{{passtype=='text'?'':''}}</el-button>
  </el-input>
</el-form-item>
<el-form-item label="" prop="sex">
<el-select v-model="form3.sex">
       <el-option label="" value=''></el-option>
      <el-option label="" value=''></el-option>
</el-select>

</el-form-item>
<el-form-item label="" prop="email">
    <el-input v-model="form3.email" clearable></el-input>
</el-form-item> 
<el-form-item label="">
     <el-date-picker
      v-model="form3.birth"
      align="right"
      type="date"
      placeholder=""
      style="width: 100%;"
      value-format="yyyy-MM-dd"
     >
    </el-date-picker>
  </el-form-item>

</el-form>	
</el-card>

<el-card class="box-card" v-if="active==3">
  <div slot="header" class="clearfix">
    <span></span>
  </div>
  <el-alert
    title=""
    type="success"
    description=""
    show-icon>
  </el-alert>
</el-card>


  <el-button type="primary" @click="handleValidRegisterCode" v-if="active==0"></el-button>
    <el-button type="primary" @click="handleValidPhoneCode" v-if="active==1"></el-button>
    <el-button type="primary" @click="handleRegister" v-if="active==2"></el-button>
</div>
</template>
<script>
	
	export default{
		created(){//
			
		}, 
		data(){
			return {
				loading :false,
				active : 0,//
				passtype :'password',//
				disabled :false ,//
				time : 120, //
				form1:{
					registerCode:''
				},
				form2:{
					phone:'',
					code:''
				},
				form3:{
					username:'',
					password:'',
					sex:'',
					phone:'',
					birth:'',
					realname:'',
					registerCode:'',
					email:''
				},
				rules2:{
					phone:[
					{ required: true, message: '', trigger: 'blur' },
					{ min: 11, max: 11, message: '11', trigger: 'blur' }
					],
					code:[
					{ required: true, message: '', trigger: 'blur' }
					]
				},
				rules3:{
					username:[
					{required:true,message:'',trigger:'blur'},
					{ min: 3, max: 18, message: '3-18', trigger: 'blur' }
					],
					password:[
					{required:true,message:'',trigger:'blur'},
					{ min: 6, max: 18, message: '6-18', trigger: 'blur' }
					]
					
				}
			}
		},
		methods:{
    	handleValidRegisterCode(){//
    		this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/validRegisterCode',
  				params: {
    				registerCode:this.form1.registerCode
  				},
  				data: {}
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
					this.active=1
					this.form3.registerCode=this.form1.registerCode
					this.loading=false
				}).catch(error => {
					this.loading=false
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
		},handleGetCode(){//
			this.disabled=true
			this.time=120
			this.downTime();
			this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/phoneCode',
  				params: {
    				phone:this.form2.phone
  				},
  				data: {}
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
				
				}).catch(error => {
					this.disabled=false
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
		},handleValidPhoneCode(){
			 this.$refs["form2"].validate((valid) => {
          if (valid) {
          	
          		this.$http({
  				method: 'get',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/validCode',
  				params: {
    				phone:this.form2.phone,
    				code:this.form2.code
  				},
  				data: {}
				}).then(response=>{
						this.$message({message:'',type:'success',showClose:true});
						this.form3.phone=this.form2.phone
          				this.active=2
				}).catch(error => {
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
          	
          	
          	
          
          	
          	  } else {
            return false;
          }
        });
		},handleRegister(){
			//this.$message({message:'',type:'success',showClose:true});
			
			 this.$refs["form3"].validate((valid) => {
          if (valid) {
				this.$http({
  				method: 'post',
  				responseType : 'json',
  				url: this.$store.state.baseUrl+'/register',
  				params: {
  				},
  				data: this.form3
				}).then(response=>{
					this.$message({message:'',type:'success',showClose:true});
					this.active=3
				}).catch(error => {
					if (error.response) {
     			 //  2xx 
      					if(error.response.status==403){
      						this.$message({message:'',type:'error',showClose:true});
      						this.$store.commit('logout')
      						this.$router.push({ path: ('/login') })
      					}else if(error.response.status==401){
      						this.$message({message:'/',type:'error',showClose:true});
      					}else if(error.response.status==400){
      						this.$message({message:error.response.data.msg,type:'error',showClose:true});
      					}
   					 } else {
      					this.$message({message:'',type:'error',showClose:true});
    				}
          })
          	
          } else {
            return false;
          }
        });
			
			
		},downTime(timer){
			if(this.disabled){
			if(timer){
				clearTimeout(timer)
			}
			if(this.time>1){
				this.time--;
			let newTimer=setTimeout(newTimer=>{
				this.downTime(newTimer)
			},1000);
			}else{
				this.disabled=false
			}
		}
		},
		 next() {
        if (this.active++ > 2) this.active = 0;
      }
		
	},computed:{
		handleLabelCode(){
				if(this.disabled){
					return this.time+'s'+' '
				}
				else{
					return ''
				}
			}
		}
		}
</script>
	
<style scoped="scoped">
.container{
		width:100%;
		margin-top: 20px;
	}	
</style>


